<template>
  <div class="app-container">
    <el-card shadow="false">
      <div class="title-main">
        <span class="title-main-left">提现申请信息</span>
        <span class="title-main-right">
          <span style="margin:0 20px">
            <el-tag v-if="details.auditType == 1"
                    type="primary">待审核</el-tag>
            <el-tag v-if="details.auditType == 5"
                    type="danger">已驳回</el-tag>
            <el-tag v-if="details.auditType == 3"
                    type="success">银行处理中</el-tag>
            <el-tag v-if="details.auditType == 2"
                    type="success">提现成功</el-tag>
            <el-tag v-if="details.auditType == 4"
                    type="warning">提现失败</el-tag>
          </span>
        </span>
      </div>
      <el-table :data="tableData"
                border
                style="width: 100%;margin-top:20px">
        <el-table-column label="提现单号"
                         prop="withdrawNo"
                         width="210">
        </el-table-column>
        <el-table-column label="微信昵称"
                         prop="nickName"
                         width="180">
        </el-table-column>
        <el-table-column prop="cellPhone"
                         label="手机号">
        </el-table-column>
        <el-table-column width="100"
                         label="提现金额">
          <template slot-scope="scope">￥{{ scope.row.withdrawAmount }}</template>
        </el-table-column>
        <el-table-column width="100"
                         label="提现到">
          <template slot-scope="scope">
            <span v-if="scope.row.type == 1">微信零钱</span>
            <span v-if="scope.row.type == 2">银行卡</span>
          </template>
        </el-table-column>
        <el-table-column width="100"
                         label="手续费">
          <template slot-scope="scope">￥{{ scope.row.withdrawCommission }}</template>
        </el-table-column>
        <el-table-column prop="createTime"
                         label="提现时间">
        </el-table-column>
      </el-table>
    </el-card>
    <el-card class="marginTop" shadow="false">
      <span class="title-main-left" v-if="details.type == 2">申请人信息</span>
      <el-form label-width="130px"
               style="margin-top:20px">
        <!-- <el-form-item label="银行名称："
                      v-if="details.auditType != 4">
          <span>{{ details.bankAccountName }}</span>
        </el-form-item> -->
        <el-form-item label="银行卡账号："
                      v-if="details.auditType != 4&&details.type == 2">
          <span>{{ details.bankAccount }}</span>
        </el-form-item>
        <el-form-item label="持卡人姓名："
                      v-if="details.auditType != 4&&details.type == 2">
          <span>{{ details.bankAccountName }}</span>
        </el-form-item>
        <el-form-item label="驳回原因："
                      v-if="details.auditType == 5">
          <span>{{ details.auditReason }}</span>
        </el-form-item>
        <el-form-item label="驳回时间："
                      v-if="details.auditType == 5">
          <span>{{ details.updateTime }}</span>
        </el-form-item>
        <el-form-item label="提现到账时间："
                      v-if="details.auditType == 2">
          <span>{{ details.payTime }}</span>
        </el-form-item>
        <el-form-item label="失败原因："
                      v-if="details.auditType == 4">
          <span>{{ details.reason }}</span>
        </el-form-item>
        <el-form-item label="银行处理时间："
                      v-if="details.auditType == 4">
          <span>{{ details.payTime }}</span>
        </el-form-item>
      </el-form>
      <el-button type="success"
                 v-if="details.auditType == 1"
                 @click="dialogAgree = true">审核通过</el-button>
      <el-button type="danger"
                 v-if="details.auditType == 1"
                 @click="dialogDisAgree = true">驳回</el-button>
      <el-button @click="goBack">返回上一页</el-button>
    </el-card>
    <!-- 审核通过 -->
    <el-dialog title="审核通过"
               :visible.sync="dialogAgree"
               width="40%">
      <span style="margin-left: 30px;font-weight:600">审核通过后，银行会发起转账汇款至对方的微信零钱或银行卡，您确定要执行该操作吗？</span>
      <div slot="footer"
           class="dialog-footer"
           v-loading="loading">
        <el-button @click="dialogAgree = false">取 消</el-button>
        <el-button type="primary"
                   @click="approval">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 驳回弹窗 -->
    <el-dialog title="驳回操作"
               :visible.sync="dialogDisAgree">
      <el-form label-width="130px">
        <el-form-item label="驳回原因：">
          <el-input v-model.trim="refundReason"
                    type="textarea"
                    autocomplete="off"
                    maxlength="20"
                    show-word-limit></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer"
           class="dialog-footer">
        <el-button @click="dialogDisAgree = false">取 消</el-button>
        <el-button type="primary"
                   @click="approvalFalse">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getUserWithdrawInfo, auditUserWithdraw } from '@/api/settleManagement/withdrawal.js'
export default {
  data() {
    return {
      tableData: [],
      details: {},
      refundDetails: {},
      dialogAgree: false,
      dialogDisAgree: false,
      refundMoney: '',
      refundReason: '',
      loading: false,
    }
  },
  created() {
    this.getDetails()
  },
  mounted() {

  },
  methods: {
    getDetails() {
      getUserWithdrawInfo({
        id: this.$route.query.id,
      }).then(res => {
        console.log(res)
        if (res.code == 200) {
          this.details = res.data
          this.tableData = [{
            withdrawNo: res.data.withdrawNo,  //提现单号
            // typeName: res.data.typeName,   //主体类型
            nickName: res.data.nickName, //昵称
            cellPhone: res.data.cellPhone,  //电话号
            type: res.data.type,  //提现渠道   1 微信  2 银行卡
            withdrawAmount: res.data.withdrawAmount,  //提现金额
            withdrawCommission: res.data.withdrawCommission,  // 手续费
            payTime: res.data.payTime,  // 支付时间
            createTime: res.data.createTime  //提现时间
          }]
        } else {
          this.$notify.error({
            title: '失败',
            message: res.message
          });
        }
      })
    },


    //提现审核操作
    approval() {
      this.loading = true
      auditUserWithdraw({
        id: this.details.id,
        auditState: 1,   //审核通过
        userId: localStorage.getItem('userId'),
        shopId: this.shop.shopId
      }).then(res => {
        if (res.code == 200) {
          this.dialogAgree = false
          this.loading = false
          this.$notify({
            title: '成功',
            message: '已审核通过，待银行处理',
            type: 'success'
          });
          this.goBack()
        } else {
          this.$notify.error({
            title: '失败',
            message: res.message,
          });
          this.dialogAgree = false
          this.loading = false
        }
      })
    },
    //驳回操作
    approvalFalse(val) {
      if (this.refundReason == '') {
        this.$message.error({
          message: '请输入驳回原因'
        });
        return
      }
      this.loading = true
      auditUserWithdraw({
        id: this.details.id,
        auditState: 2,   //审核驳回
        userId: localStorage.getItem('userId'),
        auditReason: this.refundReason,
        shopId: this.shop.shopId
      }).then(res => {
        if (res.code == 200) {
          this.dialogDisAgree = false
          this.loading = false
          this.$notify({
            title: '成功',
            message: '驳回成功',
            type: 'success'
          });
          this.goBack()
        } else {
          this.$notify.error({
            title: '失败',
            message: res.message,
          });
          this.dialogDisAgree = false
          this.loading = false
        }
      })
    },

    //返回上一页
    goBack() {
      this.$store.dispatch('delView', this.$route)
      this.$router.push({
        path: '/settleManagement/withdrawal',
      })
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="scss" scoped>
.app-container {
  padding: 20px;
  .title-main {
    display: flex;
    justify-content: space-between;
    .title-main-left {
      padding-left: 12px;
      border-left: 5px solid #ff3333;
      font-weight: 600;
      display: flex;
      align-items: center;
    }
    .title-main-right {
      float: right;
      display: flex;
      align-items: center;
    }
  }
  .title-show {
    font-size: 18px;
    font-weight: 600;
  }
  .title-reason {
    margin-top: 20px;
    font-weight: 600;
  }
}
.marginTop {
  margin-top: 10px;
  .title-main-left {
    padding-left: 12px;
    border-left: 5px solid #ff3333;
    font-weight: 600;
    display: flex;
    align-items: center;
    line-height: 32px;
  }
  .refund_details {
    margin-top: 20px;
    .refund_details_main /deep/ .el-form-item__content {
      width: 200px;
    }
  }
  .button_list {
    margin: 10px 20px;
    .button_mian {
      margin: 0 10px;
      width: 140px;
    }
  }
}
.table-layout {
  margin-top: 20px;
  border-left: 1px solid #dcdfe6;
  border-top: 1px solid #dcdfe6;
  .table-cell {
    line-height: 40px;
    border-right: 1px solid #dcdfe6;
    border-bottom: 1px solid #dcdfe6;
    padding: 10px;
    font-size: 14px;
    color: #606266;
    text-align: center;
    overflow: hidden;
  }
  .table-cell-title {
    border-right: 1px solid #dcdfe6;
    border-bottom: 1px solid #dcdfe6;
    padding: 10px;
    background: #fff;
    text-align: center;
    font-size: 14px;
    color: #303133;
    height: 60px;
    line-height: 32px;
  }
}
</style>