import request from '@/utils/request'

//列表页
export function getUserWithdrawList(data) {
  return request({
    url: `/user/business/withdraw/getUserWithdrawList`,
    method: 'post',
    data: data
  })
}
//提现详情页
export function getUserWithdrawInfo(data) {
  return request({
    url: `/user/business/withdraw/getUserWithdrawInfo?id=${data.id}`,
    method: 'get',
    data: data
  })
}
//提现审核接口
export function auditUserWithdraw(data) {
  return request({
    url: `/order/mall/pay/auditUserWithdraw`,
    method: 'post',
    data: data
  })
}




//银行卡默认手续费
export function getSysTradeRate(data) {
  return request({
    url: `/product/retailStoreProduct/getSysTradeRate`,
    method: 'get',
    data: data
  })
}

//设置银行卡默认手续费
export function setSysTradeRate(data) {
  return request({
    url: `/product/retailStoreProduct/setSysTradeRate?bankRate=${data.bankRate}&wechatRate=${data.wechatRate}`,
    method: 'get',
    data: data
  })
}


